import React from "react";
import s from "./News.module.css"
import {Navigate} from "react-router-dom";

const News = (props) => {
    if(props.isAuth === false) return <Navigate to={"/login"}/>
    return (
        <div className={s.content}>
            <h1>News</h1>
        </div>
    )
}

export default News;